@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
html {
    scroll-behavior: smooth;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Noto Sans JP', sans-serif;
    font-size: 18px;
    color: #8a8a8a;
    scroll-behavior: smooth;
}

body {
    background-color: #2a2a2a;
    
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #222;
}

.navbar-links {
    width: 100%;
    position: relative;
    flex-direction: column;
    display: flex;
    justify-content: center;
    padding-right: 2rem;
}

ul {
    list-style: none;
    flex-direction: row;
    justify-content: flex-end;
    display: flex;
}

#myNameLi{
    font-family: 'Lobster', cursive;
    width: 100%;
    text-align: left;
    padding-left: 4rem;
    font-size: 1.7rem;
    -webkit-text-decoration: #3bffe8 underline;
            text-decoration: #3bffe8 underline;
}

#resume a {
    color: #3bffe8;
    -webkit-transform-origin: (1);
            transform-origin: (1); 
    transition: .3s ease;   
}

#resume a:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

ul li a {
    position: relative;
    text-decoration: none;
    color: #cacaca;
    text-transform: uppercase;
    padding: 1rem;
    display: block;
    transition: .3s ease;
    overflow: hidden;
    font-weight: 700;
    font-size: 1em;
    transition: 0.5s;
    transition-delay: 0;
}

.navbar-links a:hover {
    /* color: rgb(230, 230, 27); */
    -webkit-transform: scale(1);
            transform: scale(1);
    color: rgb(255, 255, 255, 0.1);
    transition-delay: 0s;
}

/* enable the effects for the nav */
.navbar-links a::before {
    content: attr(data-text);
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: .35em;
    color: #3bffe8;
    box-shadow: 0 0 5px #3bffe8,
    0 0 9px #3bffe8,
    0 0 5px #3bffe8;
    font-weight: 500;
    letter-spacing: 30px;
    white-space: nowrap;
    opacity: 0;
    transition: 0.5s;
}

ul li a:hover:before{
    opacity: 1;
    letter-spacing: 6px;
    transition-delay: .3s;

} 

.toggle-button {
    position: absolute;
    top: .75rem;
    right: 1rem;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 21px;
}

.toggle-button .bar {
    height: 3px;
    width: 100%;
    background-color: #8a8a8a;
    border-radius: 10px;
}

.stick {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
}


@media (max-width: 600px) {
    .toggle-button {
      display: flex;
      margin-top: 1rem;
      z-index: 2;
      cursor: pointer;
    }
    
    .navbar-links {
      display: none;
      width: 100%;
    }

    #myNameLi{
        padding: .5rem 1rem;
        text-align: center;
    }
    
    .navbar {
      flex-direction: column;
      align-items: flex-start;
    }

    .stick {
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 1;
    }
    
    .navbar-links ul {
      width: 100%;
      flex-direction: column;
    }
    
    .navbar-links li {
      text-align: center;
    }
    
    .navbar-links li a {
      padding: .5rem 1rem;
      width: 50%;
      margin: auto;
    }
    
    .navbar-links.active {
      display: flex;
    }

    .brand-title {
        margin-top: 1.8rem;
    }
  }

