    overflow: hidden;
    position: relative;
    margin: 20px;
    text-decoration: none;
    transition: .3s ease;
}

.project p {
    color: #cacaca;
}

.colorLines {
    /* border-radius: 5px;
    max-width: 100%; */

    border-radius: 15px;
    max-width: 98%;
    position: absolute;
    height: 98%;
    right: 3px;
    
}

.smallTech {
    border-radius: 5px;
    max-width: 100%;
}


.boxLines {
    position: relative;
    width: 300px;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background: rgba(0, 0, 0, 0.5); */
    overflow: hidden;
    border-radius: 20px;
    
}

.boxLines::before {
    content: "";
    position: absolute;
    width: 160px;
    height: 160%;
    background: linear-gradient(#a1f0e6, #3bffe8);
    -webkit-animation: animateLines 4s linear infinite;
            animation: animateLines 4s linear infinite;
    z-index: -1;
}

.boxLines::after {
    content: "";
    position: absolute;
    inset: 4px;
    background: #2a2a2a;
    border-radius: 16px;
    z-index: -1;
    
}

@-webkit-keyframes animateLines {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes animateLines {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.overtop{
    background-color: #8a8a8a;
    opacity: .7;
    overflow: hidden;
    position: absolute;
    left: 3px;
    bottom: -33px;
    width: 98%;
    right: 0;
    border-bottom-left-radius: 19px;
    border-bottom-right-radius: 19px;
    -webkit-transform: translateY(101%);
            transform: translateY(101%);
    transition: -webkit-transform 0.3s ease-in;
    transition: transform 0.3s ease-in;
    transition: transform 0.3s ease-in, -webkit-transform 0.3s ease-in;
    z-index: 10;
}

.overtop a {
    display: block;
    padding: 5px 1px;
}

.overtop span {
    color: #3bffe8;
}

.project:hover .overtop{
    border-bottom: 5px;
    -webkit-transform: translateY(-35%);
            transform: translateY(-35%);
}

.overtop a:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.overtop h3 {
    
    color: #3bffe8;
    padding: 5px 1px;
}

.project:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

